.app_header{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #dcdcdc;
}

.app_header >form{
    width: 40%;
}
.task_input{
    font-size: 20px;
    font-weight: 500;
    background: #f9f9f9;
    color: black;
    border: 1px solid #b9bcc5;
    border-radius: 5px;
    padding: 8px 12px;
    margin-bottom: 15px;
    width:100%;
}
.task_input ::placeholder{
    color: #686868;
}
.search_task{
    font-size: 16px;
    font-weight: 500;
    background-color: #6457f9;
    color: #fff;
    border-radius: 5px; 
    height: 40px;
    padding: 3px 13px;
    margin-left: 10px;
    border: none;
    cursor: pointer;
}